module powerbi.visuals.sampleDataViews {
    
    export class ImageData extends SampleDataViews implements ISampleDataViewsMethods {

        public name: string = "ImageData";
        public displayName: string = "Image data";

        public visuals: string[] = ['image',
        ];

        private sampleImages = [
			"data:image/png;base64,",
			"data:image/png;base64,"
		];

        private sampleIndex: number = 0;
        private sampleData = this.sampleImages[this.sampleIndex];
    

        public getDataViews(): DataView[] {
            return [{
                metadata: {
                    columns: [],
                    objects: { general: { imageUrl: this.sampleData } }
                }
            }];
        }

        public randomize(): void {
            this.sampleIndex++; 
            if (this.sampleIndex >= this.sampleImages.length) {
                this.sampleIndex = 0; 
            }
        
            this.sampleData = this.sampleImages[this.sampleIndex];
        }
        
    }
}